<template><section><!--
 * @Author: Fone丶峰
 * @Date: 2019-10-22 11:32:29
 * @LastEditors: Fone丶峰
 * @LastEditTime: 2020-05-12 15:40:53
 * @Description: msg
 * @Email: qinrifeng@163.com
 * @Github: https://github.com/FoneQinrf
 -->
<h3>Calendar 日历选择</h3>
<hr>
<h4>使用方式</h4>
<pre v-pre><code v-pre class="language-javascript">
<span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>
<span class="hljs-keyword">import</span> { Calendar } <span class="hljs-keyword">from</span> <span class="hljs-string">'vvmui'</span>

Vue.component(Calendar)

</code></pre>
<h4>基础用法</h4>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">Calendar</span> /&gt;</span>

</code></pre>
<h4>选择单个日期</h4>
<p><code v-pre>Calendar</code>默认选择时间区间，通过<code v-pre>type</code>属性预设<code v-pre>range</code>、<code v-pre>single</code>，默认<code v-pre>range</code>，通过设置<code v-pre>single</code>可选择单个时间。</p>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">Calendar</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"single"</span> /&gt;</span>

</code></pre>
<h4>不禁用过去的日期</h4>
<p>过去的时间段时默认禁用的，通过<code v-pre>oldDateDisabled</code>属性可设置不禁用过去时间段。</p>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">Calendar</span> <span class="hljs-attr">:oldDateDisabled</span>=<span class="hljs-string">"false"</span> /&gt;</span>

</code></pre>
<h4>v-model绑定时间</h4>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Calendar</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">value</span>: [<span class="hljs-string">"2020/5/3"</span>, <span class="hljs-string">"2020/5/6"</span>]
    };
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h4>自定义禁用日期</h4>
<p>禁用这个月的今天和下个月的今天，通过<code v-pre>disabled-list</code>传递数据可自定义禁用的日期。</p>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Calendar</span> <span class="hljs-attr">:oldDateDisabled</span>=<span class="hljs-string">"false"</span> <span class="hljs-attr">:disabled-list</span>=<span class="hljs-string">"disabledList"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">disabledList</span>: []
    };
  },
  mounted() {
    <span class="hljs-keyword">const</span> date = <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>();
    <span class="hljs-keyword">this</span>.disabledList = [
      <span class="hljs-string">`<span class="hljs-subst">${date.getFullYear()}</span>/<span class="hljs-subst">${date.getMonth() + <span class="hljs-number">1</span>}</span>/<span class="hljs-subst">${date.getDate()}</span>`</span>,
      <span class="hljs-string">`<span class="hljs-subst">${date.getFullYear()}</span>/<span class="hljs-subst">${date.getMonth() + <span class="hljs-number">2</span>}</span>/<span class="hljs-subst">${date.getDate()}</span>`</span>
    ];
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h4>自定义日期solt内容</h4>
<p>通过内容分发，每个月的这一天上自定义个红点。</p>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Calendar</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"dateItem"</span> <span class="hljs-attr">slot-scope</span>=<span class="hljs-string">"{node}"</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">v-if</span>=<span class="hljs-string">"node.day === date"</span>&gt;</span>
              <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>{{node.day}}<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
              <span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"red"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>
            <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">Calendar</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">date</span>: <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>().getDate()
    };
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">style</span> <span class="hljs-attr">lang</span>=<span class="hljs-string">"less"</span> <span class="hljs-attr">scoped</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.red</span> {
  <span class="hljs-attribute">display</span>: block;
  <span class="hljs-attribute">width</span>: <span class="hljs-number">4px</span>;
  <span class="hljs-attribute">height</span>: <span class="hljs-number">4px</span>;
  <span class="hljs-attribute">border-radius</span>: <span class="hljs-number">50%</span>;
  <span class="hljs-attribute">background</span>: red;
  <span class="hljs-attribute">position</span>: absolute;
  <span class="hljs-attribute">top</span>: <span class="hljs-number">5px</span>;
  <span class="hljs-attribute">right</span>: <span class="hljs-number">10px</span>;
  <span class="hljs-attribute">z-index</span>: <span class="hljs-number">100000</span>;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>

</code></pre>
<h4>禁用</h4>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">Calendar</span> <span class="hljs-attr">disabled</span> /&gt;</span>

</code></pre>
<h3>API</h3>
<h5>Calendar props</h5>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>v-model</td>
<td>绑定值</td>
<td>String/Array</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>placeholder</td>
<td>占位文本</td>
<td>String</td>
<td>无</td>
<td>请选择时间</td>
</tr>
<tr>
<td>title-text</td>
<td>日期弹窗上的title内容</td>
<td>String</td>
<td>无</td>
<td>请选择日期</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>location-date</td>
<td>v-model不传时会根据该字段计算时间段，该字段不传默认当前时间</td>
<td>String</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>disabled-list</td>
<td>禁用日期的数组</td>
<td>Array</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>oldDateDisabled</td>
<td>是否禁用过去的时间段</td>
<td>Boolean</td>
<td>无</td>
<td>true</td>
</tr>
<tr>
<td>type</td>
<td>日期选择的模式，<code v-pre>range</code>区间选择、<code v-pre>single</code>选择单个日期</td>
<td>String</td>
<td><code v-pre>range</code> <code v-pre>single</code></td>
<td>range</td>
</tr>
<tr>
<td>start-text</td>
<td>区间选择时日期选择框下部分开始时间的内容</td>
<td>String</td>
<td>无</td>
<td>开始日</td>
</tr>
<tr>
<td>end-text</td>
<td>区间选择时日期选择框下部分结束时间的内容</td>
<td>String</td>
<td>无</td>
<td>结束日</td>
</tr>
<tr>
<td>single-text</td>
<td>单个时间选择时日期选择框下部分时间的内容</td>
<td>String</td>
<td>无</td>
<td>选中时间</td>
</tr>
<tr>
<td>confirm-text</td>
<td>日期选择框下部分右下角确认的内容</td>
<td>String</td>
<td>无</td>
<td>确认</td>
</tr>
<tr>
<td>range-separator</td>
<td>区间选择，选中时间的分割符号</td>
<td>String</td>
<td>无</td>
<td>至</td>
</tr>
<tr>
<td>formatter</td>
<td>时间格式化函数</td>
<td>Function，<code v-pre>(value) =&gt; {}</code></td>
<td>无</td>
<td>无</td>
</tr>
</tbody>
</table>
<h5>Calendar solt</h5>
<table>
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>无</td>
<td>自定义日期选择框下部分左边时间数据的内容</td>
</tr>
<tr>
<td>dateItem</td>
<td>自定义日期的内容，参数为{ node }</td>
</tr>
</tbody>
</table>
<h5>Calendar Events</h5>
<table>
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-close</td>
<td>时间选择框关闭时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-confirm</td>
<td>时间选择完点击确认触发</td>
<td>选中的时间数据</td>
</tr>
<tr>
<td>on-open</td>
<td>时间选择框打开时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-change</td>
<td>点击时间选中时触发</td>
<td>选中的时间数据和每个时间的组件内部数据，<code v-pre>(value,options) =&gt; {}</code></td>
</tr>
</tbody>
</table>
<h5>更新日志</h5>
<table>
<thead>
<tr>
<th style="text-align:center">版本</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">内容</th>
</tr>
</thead>
<tbody></tbody>
</table>
</section></template>

<script>
export default {
  data() {
    return {
      value: ["2020/5/3", "2020/5/6"]
    };
  }
};
</script>


